"use client";

type Offer = {
  id: string;
  restaurant_name: string;
  start_time: string;
  end_time: string;
  discount_percent: number;
  created_at: string;
};

function formatDateTime(value: string) {
  const date = new Date(value);
  if (Number.isNaN(date.getTime())) return value;

  return new Intl.DateTimeFormat(undefined, {
    year: "numeric",
    month: "short",
    day: "2-digit",
    hour: "2-digit",
    minute: "2-digit"
  }).format(date);
}

export function OfferCard({ offer }: { offer: Offer }) {
  return (
    <li className="rounded-xl border border-[var(--border)] bg-[var(--card)] p-4 shadow-sm">
      <div className="flex flex-wrap items-start justify-between gap-3">
        <div>
          <div className="text-base font-semibold text-[var(--foreground)]">{offer.restaurant_name}</div>
          <div className="mt-1 text-xs text-[var(--muted)]">Offer ID: {offer.id}</div>
        </div>
        <div className="rounded-full bg-slate-900 px-3 py-1 text-sm font-semibold text-white">
          {offer.discount_percent}% off
        </div>
      </div>

      <div className="mt-4 grid gap-2 text-sm text-[var(--muted)]">
        <div className="flex flex-wrap gap-2">
          <span className="min-w-12 font-semibold text-[var(--foreground)]">Start</span>
          <span title={offer.start_time}>{formatDateTime(offer.start_time)}</span>
        </div>
        <div className="flex flex-wrap gap-2">
          <span className="min-w-12 font-semibold text-[var(--foreground)]">End</span>
          <span title={offer.end_time}>{formatDateTime(offer.end_time)}</span>
        </div>
      </div>
    </li>
  );
}

